<br>
<template>
    <ga-textarea v-model="text">
        备注 <ga-icon type="edit"></ga-icon>
    </ga-textarea>
    <ga-textarea v-model="text" label="备注"></ga-textarea>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">ga-textarea</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"text"</span>&gt;</span>
