<p>带有图标标记输入类型 ( slot方式 )</p>
<br>
<br>
<template>
  <ga-textarea v-model="text" :maxLength="160" counter="">
    <ga-icon type="search" slot="pre"></ga-icon>
  </ga-textarea>
  <ga-textarea v-model="text">
    <ga-icon type="calendar" slot="append"></ga-icon>
  </ga-textarea>
</template>
