<br>
<br>
<br>
<h1>Input Attributes</h1>
  <br>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>input的类型</td>
<td>String</td>
<td>text / number / password</td>
<td>text</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>true / false</td>
<td>false</td>
</tr>
<tr>
<td>clearable</td>
<td>是否可清空</td>
<td>Boolean</td>
<td>true / false</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸</td>
<td>String</td>
<td>mini / small / medium</td>
<td>small</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框占位文本</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br>
<h1>Input Slots</h1>
  <br>
<table>
<thead>
<tr>
<th>name</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>pre</td>
<td>输入框前置内容</td>
</tr>
<tr>
<td>append</td>
<td>输入框后置内容</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br> 
<h1>Input Events</h1>
  <br>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>(value: string / number)</td>
</tr>
<tr>
<td>blur</td>
<td>在 Input 失去焦点时触发</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>focus</td>
<td>在 Input 获得焦点时触发</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>preiconclicked</td>
<td>前置icon点击事件</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>appendiconclicked</td>
<td>后置icon点击事件</td>
<td>(event: Event)</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br> 
<h1>Input Methods</h1>
  <br>
<table>
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>使 input 获取焦点</td>
<td>—</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br> 
</template>


</section></template>
