<template><section><template>
<blockquote>
<p>Radio 单选框</p>
</blockquote>
  <br> 
<h5>在一组备选项中进行单选</h5>
<br>
<br>
<br>
<h3><strong>基础用法</strong></h3>
<br>
<p>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
<br>
<br>
  <template>
    <ga-radio v-model="radioData" label="radio1">单选框1</ga-radio>
    <ga-radio v-model="radioData" label="radio2">单选框2</ga-radio>
  </template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioData"</span>  <span class="hljs-attr">label</span>=<span class="hljs-string">"radio1"</span>&gt;</span>单选框1<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioData"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"radio2"</span>&gt;</span>单选框2<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>radio-group</strong></h3>
<br>
<p>结合<code v-pre="">ga-radio-group</code>元素和子元素<code v-pre="">ga-radio</code>可以实现单选组，在<code v-pre="">ga-radio-group</code>中绑定<code v-pre="">v-model</code>，在<code v-pre="">ga-radio</code>中设置好<code v-pre="">label</code>即可，无需再给每一个<code v-pre="">ga-radio</code>绑定变量，另外，还提供了<code v-pre="">change</code>事件来响应变化，它会传入一个参数<code v-pre="">value</code>。</p>
<br>
<br>
<template>
  <ga-radio-group @change="labelChange" v-model="radioGroup">
    <ga-radio label="radio-group1">radio-group1</ga-radio>
    <ga-radio label="radio-group2">radio-group2</ga-radio>
    <ga-radio label="radio-group3">radio-group3</ga-radio>
  </ga-radio-group>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio-group</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"labelChange"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioGroup"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"radio-group1"</span>&gt;</span>radio-group1<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"radio-group2"</span>&gt;</span>radio-group2<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"radio-group3"</span>&gt;</span>radio-group3<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio-group</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h3><strong>禁用状态</strong></h3>
<br>
<p>单选框不可用的状态。只要在<code v-pre="">ga-radio</code>元素中设置<code v-pre="">disabled</code>属性即可，它接受一个<code v-pre="">Boolean</code>，<code v-pre="">true</code>为禁用。</p>
<br>
<br>
<template>
  <ga-radio-group @change="labelChange" v-model="radioGroup2">
    <ga-radio disabled label="禁用状态">禁用状态</ga-radio>
    <ga-radio label="可用状态">可用状态</ga-radio>
  </ga-radio-group>
</template>
<br>
<br>
<pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio-group</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"labelChange"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioGroup2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"禁用状态"</span>&gt;</span>禁用状态<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">ga-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"可用状态"</span>&gt;</span>可用状态<span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">ga-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<br>
<br>
<br>
<h1>Radio Attributes</h1>
  <br>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>Radio 的 value</td>
<td>String / Number / Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>true / false</td>
<td>—</td>
</tr>
<tr>
<td>size</td>
<td>Radio 的尺寸</td>
<td>String</td>
<td>medium / small / mini</td>
<td>—</td>
</tr>
</tbody>
</table>
  <br>
  <br>
  <br>
<h1>Radio-group Events</h1>
  <br>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调函数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>绑定值变化时触发的事件</td>
<td>选中的 Radio label 值</td>
</tr>
</tbody>
</table>
</template>


</section></template>
<style lang="scss">
    .ga-radio{
        padding-right:12px;
    }
</style>
<script>
  export default {
    data () {
      return {
          radioData:'radio1',
          radioGroup:"radio-group1",
          radioGroup2:"禁用状态"
      }
    },
    methods:{
      labelChange(val){
        console.log(val)
      }
    }
  }
</script>


